/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body {
    background-color: #10171d;
    color: #fff;
}
img,video {
    width: 300px;
    height: 170px;
}
.container {
    max-width: 1200px;
    margin: 0 auto;
}
h1 {
    display: block;
}
button {
    padding: 10px;
    border: 0;
}
.ItemDetails img, .ItemDetails video{
    width: auto;
    height: 500px;
    display: block;
    margin: 50px auto;
}
.ItemDetails audio{
    display: block;
    margin: 50px auto;
}
.ListItem {
    text-align: center;
    padding: 20px;
    margin: 20px;
    background-color: #222b32;
}
.ListItem:hover {
    background-color: #333c43;
}
.ListItem .title {
    margin-top: 20px;
    color: #fff;
}
.ItemsList {
    display: grid;
    grid-column-gap: 50px;
    grid-template-columns: auto auto auto;
}
.ItemDetails .title {
    font-size: 30px;
    text-align: center;
}
.ItemDetails .description {
    margin: 50px auto; 
    width: 700px;
    line-height: 20px;
}
header {
    background-image: url(/nasa/static/media/bg.30a6d11c.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 200px;
}
.Filter {
    background: #10171d;
    padding: 20px 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.Filter a {
    color: #fff;
    text-decoration: none;
}
.Filter form {
    margin-left: 20px;
}
.Filter form input {
    font-size: 20px;
    background: #222b32;
    border: 0;
    color: #fff;
    margin: 10px;
}
.Filter .text-input {
    padding: 5px;
}
.Filter .years {
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.Filter .rc-slider {
    margin: 0 20px;
}
.Filter svg {
    display: block;
    box-sizing: border-box;
}
.Filter .submit {
    margin-right: 20px;
}

.FilterMain {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.FilterMain header {
    width: 100%;
    height: 400px;
}
